import { FormaPagamentoDTO } from './../../../../../models/domain/forma-pagamennto.dto';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Component, Output, ViewChild, OnInit, EventEmitter, Input } from '@angular/core';

import { AbstractPopupComponent } from 'config/core/abstract-popup.component';
import { FormaPagamentoService } from './../../formapagamento/forma-pagamento.service';

@Component({
  selector: 'forma-pagamento-popup-modal',
  templateUrl: './forma-pagamento-popup-modal.component.html',
  styleUrls: ['./forma-pagamento-popup-modal.component.scss']

})
export class FormaPagamentoPopupModalComponent extends AbstractPopupComponent implements OnInit {


  item: FormaPagamentoDTO;


  constructor(
    public pagService: FormaPagamentoService,
    public fb: FormBuilder) {
    super('formaPagamentoPopup');
  }

  ngOnInit() {
    this.criarForm();
  }

  show() {
    this.criarForm();
    this.showPopup("Cadastra forma de pagamento");
  }

  showAlteracao(item: FormaPagamentoDTO) {
    this.alterarForm(item)
    this.showPopup("Alterar forma de pagamento");

  }

  btnConfirmar(item: FormaPagamentoDTO) {
    this.acao(item, this.pagService);
  }

  criarForm() {
    this.formulario = this.fb.group({
      id: [''],
      nmFormaPagamento: ['', Validators.required],
      emAlteracao: [false]
    })
  }
  

  alterarForm(item: FormaPagamentoDTO) {
    this.formulario.patchValue({
      id: item.id,
      nmFormaPagamento: item.nmFormaPagamento,
      emAlteracao: true
    })
  }

}



