import { FormaPagamentoService } from './../forma-pagamento.service';
import { FormaPagamentoDTO } from './../../../../../models/domain/forma-pagamennto.dto';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';


import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { AbstractBaseComponent } from 'config/core/abstract-base.component';
import { TrataErrorService } from 'services/trata-error.service';


@Component({
  templateUrl: './listar-forma-pagamento.component.html',
})
export class ListarFormaPagamentoComponent extends AbstractBaseComponent implements OnInit {

  @ViewChild('excluirModal') popupExclusao;
  @ViewChild('changeSatusModal') popupChangeStatus;

  item: FormaPagamentoDTO;
  items: FormaPagamentoDTO[];

  constructor(
    public pagService: FormaPagamentoService,
    public router: Router) {
    super();
  }

  ngOnInit() {
    this.carregarTodos();
  }

  selecionarItem(item: FormaPagamentoDTO) {
    this.item = item;
    this.popupExclusao.show();
  }

  selecionarItemChangeStatus(item: FormaPagamentoDTO) {
    this.item = item;
    this.popupChangeStatus.show();
  }

  changeStatus() {
    this.pagService.changeStatus(this.item)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Forma de Pagamento', 'O status da forma de pagamento foi alterado com sucesso!!!')
        this.carregarTodos()
      }, error => TrataErrorService.tratarError(error));
  }

  excluir() {
    this.pagService.excluir(this.item)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Forma de Pagamento', 'A forma de pagamento foi excluída com sucesso!!!')
        this.carregarTodos()
      }, error => TrataErrorService.tratarError(error));
  }

  private carregarTodos() {
    this.pagService.findAll().subscribe(response => {
      this.items = response
    }, error => TrataErrorService.tratarError(error));
  }

}



