import { FormaPagamentoRoutingModule } from './forma-pagamento-routing.module';

import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { PopupModalModule } from 'app/shared/popup/popup-modal.module';
import { ModalModule } from 'ng2-bootstrap/modal';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';

import { SharedComponentModule } from './../../../shared/shared-component.module';
import { FormaPagamentoService } from './forma-pagamento.service';
import { ListarFormaPagamentoComponent } from './listar/listar-forma-pagamento.component';
import { FormaPagamentoPopupModalComponent } from './popup/forma-pagamento-popup-modal.component';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ModalModule.forRoot(),
    SharedComponentModule,
    ReactiveFormsModule,
    PopupModalModule,
    HttpClientModule,
    FormaPagamentoRoutingModule
  ],
  declarations: [
    ListarFormaPagamentoComponent,
    FormaPagamentoPopupModalComponent
  ],
  providers:[
    FormaPagamentoService
  ]
})
export class FormaPagamentoModule { }



